import React, {ReactElement} from 'react'
import Dish from './Dish'

import dishes from '../resources/dishes.json'
import Filters from './Filters'

const dishTypeFilters = ['', '', '', '', '', '', '', '', '', '', '', '', '']
const dishQuicknessFilters = ['', '-', '', '-', '', '-', '  ' ]
const dishHowFilters = ['', '', '', '', '', '', '', '', '', '', ' ',  '', '' ]
const dishDiateryTypeFilters = ['', '', '', '']
const dishMeatFilters = ['', '', '', '', '', '', '']
const dishPlantsFilters = ['', '', '', '', '', '', '', '',  ' ', '', '', '', '', '', '', '', '', '']
const dishAnimalProductsFilters=['', '', '', ' ', ' ', '', '']

const filters = dishes.reduce((sum: any, dish) => {
    const newSum = [...sum]
    for (const keyword of dish.keywords) {
        if (!sum.includes(keyword)
            && !dishTypeFilters.includes(keyword)
            && !dishQuicknessFilters.includes(keyword)
            && !dishHowFilters.includes(keyword)
            && !dishDiateryTypeFilters.includes(keyword)
            && !dishMeatFilters.includes(keyword)
            && !dishPlantsFilters.includes(keyword)
            && !dishAnimalProductsFilters.includes(keyword)){
            newSum.push(keyword)
        }
    }

    return newSum
}, [])

function CookBook (): ReactElement {
    const [filtered, setFiltered] = React.useState<string[]>([])

    const onClickFilter = (filter: string) => {
        if (filtered.includes(filter)) {
            const newFiltered = filtered.filter(item => item != filter)
            setFiltered(newFiltered)
        } else {
            const newFiltered = [...filtered, filter]
            setFiltered(newFiltered)
        }
    }

    const filteredDishes = dishes.filter(dish => {
        if (filtered.length === 0) return true
        for (const filter of filtered) {
            if (!dish.keywords.includes(filter)) return false
        }
        return true
    })

    return (
        <div>
            <Filters title={' :'} filters={dishTypeFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishQuicknessFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishHowFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishDiateryTypeFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishMeatFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishPlantsFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={dishAnimalProductsFilters} filtered={filtered} onClickFilter={onClickFilter}/>
            <Filters title={':'} filters={filters} filtered={filtered} onClickFilter={onClickFilter}/>
            <div style={{
                display: 'flex',
                flexWrap: 'wrap',
            }}>
                {filteredDishes.map(dish => <Dish key={dish.title} dish={dish} />)}
            </div>
        </div>
    )
}

export default CookBook